@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
*{margin:0;padding:0}body{display:-webkit-flex;display:flex;font-size:15px;font-weight:500;font-family:'Montserrat', sans-serif;background-color:#4b9aaa}

.todoTitle{display:-webkit-flex;display:flex;font-weight:400}.todoCompleted{display:-webkit-flex;display:flex}

.todoInfo{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:10px;grid-gap:10px;gap:10px;background-color:#cd4d31}.todoList{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px;margin:20px}

